
.home-page {
  height: 100vh;

  &__board {
    margin: auto;
    margin-top: 20vh;
    width: calc(90vw - 40px);
    max-width: 650px;
    border: solid 5px rgba(0, 0, 0, 0.7);
    text-align: center;
    padding: 20px;

    &__content {
      &__info {
        margin-top: 20px;
      }

      &__start {
        display: flex;
        justify-content: space-evenly;
        margin-top: 20px;

        & > div {
          font-size: 1.5rem;
          cursor: pointer;
        }
      }
    }
  }
}
