
.board {
  border: solid 5px rgba(0, 0, 0, 0.7);
  width: 90vw;
  max-width: 690px;
  margin: auto;
  margin-top: 10vh;

  &__inner {
    padding-top: 66.66%;
    position: relative;
  }

  &__item-container {
    display: grid;
    gap: 2px;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;

    .item {
      border-radius: 2px;
    }

    .item-snake {
      background-color: rgba(0, 0, 0, 0.7);
    }

    .item-food {
      background-color: rgba(255, 0, 0, 0.7);
    }
  }
}

.dialog {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 1000;

  &__mask {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 1000;
  }

  &__body {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80%;
    max-width: 690px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
    z-index: 1001;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    &__title {
      font-size: 20px;
      font-weight: 600;
      color: #333;
    }

    &__content {
      font-size: 14px;
      color: #666;
      margin-top: 10px;
    }

    &__btns {
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      width: 100%;
      margin-top: 20px;

      .primary {
        background-color: #333;
        color: #fff;
      }

      .default {
        background-color: #fff;
        color: #333;
      }

      .dialog__btn {
        padding: 1px 15px;
        background-color: #fff;
        color: #333;
        border-radius: 10px;
        text-align: center;
        line-height: 40px;
        cursor: pointer;
      }
    }
  }
}
